.header {
  background-color: var(--color-light);
  border-bottom: 2px solid;
  border-color: var(--color-border);
  // box-shadow: 0px 5px 5px 0px var(--color-shudow);



  .container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 1.4rem;
    padding: .5rem 1rem;
  }


  .header__left {
    display: flex;
    align-items: center;
  }

  .logo {
    display: block;
    width: 15.6rem;
    height: 3.8rem;
    margin-right: 4rem;
    margin-left: 1rem;

  }


  .header__right {
    display: flex;
    align-items: center;

    >div {
      margin-right: 3rem;
    }

    .grafigs {
      ._icon-grafigs {
        color: var(--color-secondery);
      }
    }
  }

  .messages {
    position: relative;
  }

  .message__bell {
    display: flex;
    justify-content: center;
    align-items: center;

    ._icon-bell {
      color: var(--color-secondery);
    }

    .message__attention {
      display: flex;
      justify-content: center;
      align-items: center;

      position: absolute;
      top: -.9rem;
      right: -.7rem;
      width: 1.5rem;
      height: 1.5rem;
      padding-top: .1rem;

      font-size: 1rem;
      color: var(--color-light);

      box-sizing: border-box;
      background-color: var(--color-message);
      border-radius: 50%;
    }

    #bell__toggle {
      position: absolute;
      top: -9px;
      right: -9px;
      height: 2.5rem;
      width: 2.5rem;
      opacity: 0;
      cursor: pointer;
      z-index: 2;
    }

  }

  .massege__container {
    display: flex;
    flex-direction: column;
    align-items: start;

    font-size: 1.2rem;

    position: absolute;
    min-width: 23rem;
    top: 3.3rem;
    right: 0;
    padding: 2rem 1.5rem 1rem 1.5rem;

    background-color: var(--color-light);
    box-shadow: 0px 0px 8px 2px var(--color-shudow);

    visibility: hidden;
    opacity: 0;
    transform: translate(0, -150%);
    transition: all .3s;
    z-index: 2;



    h4 {
      font-size: 1rem;
      color: var(--color-secondery);
      margin-bottom: 2rem;
    }

    ul {

      li {
        display: flex;
        justify-content: start;
        align-items: center;

        padding: 1em 1.2em;
        margin-bottom: 1rem;
        border: 1px solid var(--color-border);

        &:nth-child(1n) {
          border-left: 4px solid var(--color-border-first);
        }

        &:nth-child(2n) {
          background-color: var(--color-bg-main);
          border-left: 4px solid var(--color-border-second);
        }

      }



    }

    .all-messages {
      position: relative;
      display: block;
      padding: 1em .5em;
      width: 100%;

      text-align: left;

      &::after {
        content: "";
        display: block;
        position: absolute;
        right: .5em;
        top: 50%;
        width: 6px;
        height: 6px;
        border-right: 2px solid;
        border-bottom: 2px solid;
        border-color: var(--color-black);
        transform: rotate(-45deg);
      }
    }

    .all-messages:hover {
      border-bottom: 1px solid var(--color-border);
    }

  }

  #bell__toggle:checked~.massege__container {
    visibility: visible;
    opacity: 1;
    transform: translate(0, 0);
    transition: all .3s;
  }

  .user {
    position: relative;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 1.4rem;
    margin-right: 2rem;
  }

  .user__info {
    position: relative;
    font-size: 1.2rem;
    z-index: 3;
  }

  .user__icon {
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: 4rem;
    height: 4rem;
    margin-right: 1rem;
    border-radius: 50%;
    background-color: var(--color-border);

    z-index: 3;

    span {
      display: block;
      color: #fff;
      font-size: 2rem;
    }
  }

  .user__name {
    font-weight: bold;
    margin-right: 2rem;
    margin-bottom: .5rem;
  }

  .user__access {

    margin-right: 2rem;
    color: var(--color-secondery);
  }

  .user__btn-placing {
    position: absolute;
    right: 0;
    top: 1.5rem;
    transition: all .3s;
    z-index: 3;
  }

  .user__container {
    display: flex;
    flex-direction: column;
    align-items: start;

    font-size: 1.2rem;

    position: absolute;
    min-width: 200px;
    top: -.5rem;
    right: -1.7rem;
    padding: 6rem 0rem 0rem 0rem;

    background-color: var(--color-light);
    box-shadow: 0px 0px 8px 2px var(--color-shudow);

    visibility: hidden;
    opacity: 0;
    transform: translate(0, -150%);
    transition: all .3s;

    z-index: 2;

    ul {
      display: block;
      width: 100%;
      margin-bottom: 4rem;

      li {
        padding: 1rem 1.5rem;
        border-bottom: 1px solid var(--color-border);


      }
    }
  }

  #user__toggle {
    position: absolute;
    right: -.3rem;

    height: 2rem;
    width: 2rem;

    opacity: 0;
    cursor: pointer;
    z-index: 4;

  }

  #user__toggle:checked~.user__container {
    visibility: visible;
    opacity: 1;
    transform: translate(0, 0);
    transition: all .3s;
  }

  #user__toggle:checked~label>.btn-select {
    transform: rotate(-135deg);
    transition: all .3s;
  }


}


@media (max-width: 624px) {
  .header {
    .logo {
      width: 7.2rem;
      height: 3.8rem;
      margin-right: 2rem;
    }

  }
}

@media (max-width:530px) {
  .header {
    .logo {
      margin: 0;
      margin-right: 1.5rem;
    }



    .header__right>div {
      margin-right: 1.5rem;
    }

    .massege__container {
      right: -2rem;
    }

    .grafigs,
    .languages,
    .btn-question,
    .user__info {
      display: none;
    }

    .user__container {
      top: 4.6rem;
      padding: 0;
      right: -1.5rem;

    }
  }

}
