@charset "UTF-8";
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-active: #1a85cd;
  --color-message: #bf1a16;
  --color-secondery: #7c7f7e;
  --color-light: #fefefe;
  --color-bg-main: #e5e6e6;
  --color-bg-nav: #2b323a;
  --color-bg-icon: #55AC77;
  --color-bg-chat-manadger: #DEF2F9;
  --color-bg-chat-user: #E4FFDE;
  --color-bg-link: rgba(32, 37, 43, .5);
  --color-hover-link: rgba(11, 13, 15, 0.5);
  --color-border: #c8cac9;
  --color-border-first: #ff9800;
  --color-border-second: #2196f3;
  --color-shudow: rgba(180, 180, 180, .8);
}

.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

@font-face {
  font-family: "icons";
  src: url("../fonts/icons.ttf?9zyo3u") format("truetype"), url("../fonts/icons.woff?9zyo3u") format("woff"), url("../fonts/icons.svg?9zyo3u#icons") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
[class^=_icon-]::before,
[class*=" _icon-"]::before {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icons" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

._icon-grafigs:before {
  content: "\e901";
}

._icon-folders2:before {
  content: "\e902";
}

._icon-paper:before {
  content: "\e905";
}

._icon-bell:before {
  content: "\e900";
}

._icon-chat:before {
  content: "\e903";
}

._icon-diagrams:before {
  content: "\e904";
}

._icon-folders:before {
  content: "\e906";
}

._icon-folders21:before {
  content: "\e907";
}

._icon-gear:before {
  content: "\e908";
}

._icon-grafigs1:before {
  content: "\e909";
}

._icon-home:before {
  content: "\e90a";
}

._icon-icon-slider:before {
  content: "\e90b";
}

._icon-maseg:before {
  content: "\e90c";
}

._icon-peaple:before {
  content: "\e90e";
}

._icon-phone:before {
  content: "\e90f";
}

._icon-screen-serch:before {
  content: "\e910";
}

._icon-folder-open:before {
  content: "\e930";
}

._icon-switch:before {
  content: "\e9b6";
}

._icon-dropbox:before {
  content: "\eaae";
}

/*Обнуление*/
*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  line-height: 1;
  font-size: 10px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  font-size: 1.6rem;
  font-family: "Arial", sans-serif;
  background-color: var(--color-bg-main);
  color: var(--color-black);
}

img {
  max-width: 100%;
}

a,
a:focus,
a:active,
a:hover,
a:hover,
a:visited {
  outline: none;
  text-decoration: none;
  color: inherit;
}

input,
button,
textarea {
  font-family: inherit;
}

.site-container {
  overflow: hidden;
  min-height: 100vh;
  display: grid;
  grid-template-areas: "header" "main" "footer";
  grid-template-rows: auto 1fr auto;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}
.btn-reset:focus-visible {
  outline: 0;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  background-color: var(--color-light);
  border-bottom: 2px solid;
  border-color: var(--color-border);
}
.header .container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 1.4rem;
  padding: 0.5rem 1rem;
}
.header .header__left {
  display: flex;
  align-items: center;
}
.header .logo {
  display: block;
  width: 15.6rem;
  height: 3.8rem;
  margin-right: 4rem;
  margin-left: 1rem;
}
.header .header__right {
  display: flex;
  align-items: center;
}
.header .header__right > div {
  margin-right: 3rem;
}
.header .header__right .grafigs ._icon-grafigs {
  color: var(--color-secondery);
}
.header .messages {
  position: relative;
}
.header .message__bell {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .message__bell ._icon-bell {
  color: var(--color-secondery);
}
.header .message__bell .message__attention {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -0.9rem;
  right: -0.7rem;
  width: 1.5rem;
  height: 1.5rem;
  padding-top: 0.1rem;
  font-size: 1rem;
  color: var(--color-light);
  box-sizing: border-box;
  background-color: var(--color-message);
  border-radius: 50%;
}
.header .message__bell #bell__toggle {
  position: absolute;
  top: -9px;
  right: -9px;
  height: 2.5rem;
  width: 2.5rem;
  opacity: 0;
  cursor: pointer;
  z-index: 2;
}
.header .massege__container {
  display: flex;
  flex-direction: column;
  align-items: start;
  font-size: 1.2rem;
  position: absolute;
  min-width: 23rem;
  top: 3.3rem;
  right: 0;
  padding: 2rem 1.5rem 1rem 1.5rem;
  background-color: var(--color-light);
  box-shadow: 0px 0px 8px 2px var(--color-shudow);
  visibility: hidden;
  opacity: 0;
  transform: translate(0, -150%);
  transition: all 0.3s;
  z-index: 2;
}
.header .massege__container h4 {
  font-size: 1rem;
  color: var(--color-secondery);
  margin-bottom: 2rem;
}
.header .massege__container ul li {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 1em 1.2em;
  margin-bottom: 1rem;
  border: 1px solid var(--color-border);
}
.header .massege__container ul li:nth-child(1n) {
  border-left: 4px solid var(--color-border-first);
}
.header .massege__container ul li:nth-child(2n) {
  background-color: var(--color-bg-main);
  border-left: 4px solid var(--color-border-second);
}
.header .massege__container .all-messages {
  position: relative;
  display: block;
  padding: 1em 0.5em;
  width: 100%;
  text-align: left;
}
.header .massege__container .all-messages::after {
  content: "";
  display: block;
  position: absolute;
  right: 0.5em;
  top: 50%;
  width: 6px;
  height: 6px;
  border-right: 2px solid;
  border-bottom: 2px solid;
  border-color: var(--color-black);
  transform: rotate(-45deg);
}
.header .massege__container .all-messages:hover {
  border-bottom: 1px solid var(--color-border);
}
.header #bell__toggle:checked ~ .massege__container {
  visibility: visible;
  opacity: 1;
  transform: translate(0, 0);
  transition: all 0.3s;
}
.header .user {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 1.4rem;
  margin-right: 2rem;
}
.header .user__info {
  position: relative;
  font-size: 1.2rem;
  z-index: 3;
}
.header .user__icon {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 4rem;
  height: 4rem;
  margin-right: 1rem;
  border-radius: 50%;
  background-color: var(--color-border);
  z-index: 3;
}
.header .user__icon span {
  display: block;
  color: #fff;
  font-size: 2rem;
}
.header .user__name {
  font-weight: bold;
  margin-right: 2rem;
  margin-bottom: 0.5rem;
}
.header .user__access {
  margin-right: 2rem;
  color: var(--color-secondery);
}
.header .user__btn-placing {
  position: absolute;
  right: 0;
  top: 1.5rem;
  transition: all 0.3s;
  z-index: 3;
}
.header .user__container {
  display: flex;
  flex-direction: column;
  align-items: start;
  font-size: 1.2rem;
  position: absolute;
  min-width: 200px;
  top: -0.5rem;
  right: -1.7rem;
  padding: 6rem 0rem 0rem 0rem;
  background-color: var(--color-light);
  box-shadow: 0px 0px 8px 2px var(--color-shudow);
  visibility: hidden;
  opacity: 0;
  transform: translate(0, -150%);
  transition: all 0.3s;
  z-index: 2;
}
.header .user__container ul {
  display: block;
  width: 100%;
  margin-bottom: 4rem;
}
.header .user__container ul li {
  padding: 1rem 1.5rem;
  border-bottom: 1px solid var(--color-border);
}
.header #user__toggle {
  position: absolute;
  right: -0.3rem;
  height: 2rem;
  width: 2rem;
  opacity: 0;
  cursor: pointer;
  z-index: 4;
}
.header #user__toggle:checked ~ .user__container {
  visibility: visible;
  opacity: 1;
  transform: translate(0, 0);
  transition: all 0.3s;
}
.header #user__toggle:checked ~ label > .btn-select {
  transform: rotate(-135deg);
  transition: all 0.3s;
}

@media (max-width: 624px) {
  .header .logo {
    width: 7.2rem;
    height: 3.8rem;
    margin-right: 2rem;
  }
}
@media (max-width: 530px) {
  .header .logo {
    margin: 0;
    margin-right: 1.5rem;
  }
  .header .header__right > div {
    margin-right: 1.5rem;
  }
  .header .massege__container {
    right: -2rem;
  }
  .header .grafigs,
.header .languages,
.header .btn-question,
.header .user__info {
    display: none;
  }
  .header .user__container {
    top: 4.6rem;
    padding: 0;
    right: -1.5rem;
  }
}
.main {
  display: flex;
}
.main .container {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  padding: 2rem 3rem;
}
.main .meassage {
  display: flex;
  min-height: 30rem;
  width: 100%;
  font-size: 1.8rem;
  padding: 1rem;
  margin-bottom: 3rem;
  background-color: var(--color-light);
  border: 1px solid var(--color-border);
}

.tabs {
  margin-bottom: 2rem;
}
.tabs input {
  display: none;
}
.tabs input:checked + label {
  color: var(--color-black);
  background-color: var(--color-light);
}
.tabs label {
  display: inline-block;
  padding: 1rem 2rem;
  margin-right: 0.2rem;
  color: var(--color-secondery);
  background-color: var(--color-border);
  border-top: 1px solid;
  border-left: 1px solid;
  border-right: 1px solid;
  border-color: var(--color-border);
  cursor: pointer;
}
.tabs .tabs__block {
  display: none;
  width: 100%;
  min-height: 20rem;
  font-size: 1.8rem;
  line-height: 2rem;
  padding: 2rem;
  background-color: var(--color-light);
  border-left: 1px solid;
  border-right: 1px solid;
  border-bottom: 1px solid;
  border-color: var(--color-border);
}
.tabs input[id=tab-1]:checked ~ .tabs__body .tab-1,
.tabs input[id=tab-2]:checked ~ .tabs__body .tab-2,
.tabs input[id=tab-3]:checked ~ .tabs__body .tab-3 {
  display: block;
}

@media (max-width: 624px) {
  .main .container {
    padding: 2rem;
  }
}
.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  width: 100%;
  font-size: 1.2rem;
  background-color: var(--color-light);
  border: 1px solid var(--color-border);
}
.footer .copyright {
  font-weight: 700;
}
.footer .sp-active {
  font-weight: 700;
  color: var(--color-active);
}

.breadcrumbs {
  padding: 10px 16px;
  /* Отображение элементов списка бок о бок */
}
.breadcrumbs li {
  display: inline;
  font-size: 1.4rem;
}
.breadcrumbs li a span {
  font-size: 1.8rem;
  margin-right: 1rem;
}

/* Добавить символ косой черты (/) перед/за каждым элементом списка */
.breadcrumbs li + li:before {
  padding: 8px;
  color: black;
  content: "/ ";
}

/* Добавить цвет на наведении курсора мыши */
.breadcrumbs li a:hover {
  color: var(--color-active);
  text-decoration: underline;
}

.chat {
  position: fixed;
  right: 20px;
  bottom: 50px;
  opacity: 0;
  visibility: hidden;
  transform: scaleX(0);
  transform-origin: right center;
  transition: 0.3s ease;
  z-index: 10;
}

.chat__container {
  display: flex;
  flex-direction: column;
  width: 375px;
  height: 550px;
  background-color: var(--color-bg-main);
  box-shadow: 0px 0px 8px 2px var(--color-shudow);
  border-radius: 10px;
}

.chat__header {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  color: var(--color-white);
  background-color: var(--color-active);
  border-radius: 10px 10px 0 0;
}

.admin-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  margin-right: 15px;
  border-radius: 50%;
  background-color: var(--color-bg-icon);
}

._icon-peaple {
  font-size: 3.5rem;
}

.admin-info {
  margin-right: 1rem;
}

.admin-name {
  font-size: 2.2rem;
  line-height: 2.6rem;
  margin-bottom: 0.5rem;
}

.admin-access {
  font-size: 1.2rem;
  letter-spacing: 1.2px;
}

.chat__body {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-y: auto;
  padding: 0 0.5rem;
  margin: 1rem;
  border-bottom: 1px solid var(--color-border);
}

.chat__title {
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.1rem;
  text-align: center;
  margin-bottom: 1.5em;
}

.message__manager,
.message__user {
  font-size: 1.4rem;
  margin-bottom: 1.5rem;
}

.message__info {
  font-size: 1.2rem;
  color: var(--color-secondery);
  margin-bottom: 1rem;
}

.message__manager-text {
  position: relative;
  display: block;
  width: 90%;
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: var(--color-bg-chat-manadger);
  border: 1px solid var(--color-border);
  border-radius: 5px;
}
.message__manager-text::before, .message__manager-text::after {
  position: absolute;
  top: -8px;
  left: 8px;
  display: block;
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 8px 8px 8px;
  border-color: transparent transparent var(--color-border) transparent;
}
.message__manager-text::after {
  position: absolute;
  top: -6px;
  left: 8px;
  border-color: transparent transparent var(--color-bg-chat-manadger) transparent;
}

.message__user {
  text-align: right;
}

.message__user-text {
  position: relative;
  display: block;
  width: 90%;
  text-align: left;
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: var(--color-bg-chat-user);
  border: 1px solid var(--color-border);
  border-radius: 5px;
}
.message__user-text::before, .message__user-text::after {
  position: absolute;
  top: -8px;
  right: 8px;
  display: block;
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 8px 8px 8px;
  border-color: transparent transparent var(--color-border) transparent;
}
.message__user-text::after {
  position: absolute;
  top: -6px;
  right: 8px;
  border-color: transparent transparent var(--color-bg-chat-user) transparent;
}

.chat__footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0.5rem 1rem 1rem;
  height: 10rem;
}

.chat__input {
  display: flex;
  width: 100%;
  height: 100%;
  resize: none;
  padding: 1rem;
  background-color: var(--color-bg-chat-user);
  border: 1px solid var(--color-border);
  border-radius: 5px;
}
.chat__input::placeholder {
  font-size: 1.6rem;
  color: var(--color-shudow);
}
.chat__input:focus-visible, .chat__input:hover {
  outline: 0;
  background-color: var(--color-light);
}

.chat-btns {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
}
.chat-btns svg {
  fill: var(--color-border);
  transition: 0.3s ease;
}
.chat-btns svg:hover {
  fill: var(--color-active);
  transition: 0.3s ease;
}

.chat__send {
  margin-bottom: 1rem;
}

.chat-active {
  opacity: 1;
  visibility: visible;
  transform: scaleX(1);
  transform-origin: right center;
  transition: 0.3s ease;
}

@media (max-width: 413px) {
  .chat {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .chat__container {
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 385px) {
  .chat {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .chat__container {
    width: 100%;
    height: 100%;
  }

  .admin-icon {
    width: 50px;
    height: 50px;
