  }
}
@media (max-height: 600px) {
  .chat {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .chat__container {
    width: 100%;
    height: 100%;
  }

  .admin-icon {
    width: 50px;
    height: 50px;
    margin-right: 15px;
  }

  .admin-name {
    font-size: 1.8rem;
    line-height: 2.2rem;
  }
}
.nav-admin {
  min-width: auto;
  font-size: 2.4rem;
  padding-top: 0.8rem;
  margin-top: -0.2rem;
  background-color: var(--color-bg-nav);
  transition: all 0.3s;
}
.nav-admin ul {
  width: 100%;
  transition: all 0.3s;
}
.nav-admin li {
  width: 100%;
  margin-bottom: 0.2em;
  background-color: var(--color-bg-link);
}
.nav-admin li:last-child {
  margin-bottom: 5em;
}
.nav-admin li a {
  display: flex;
  align-items: center;
  padding: 0.5rem;
  padding-left: 0.8rem;
  color: var(--color-white);
}
.nav-admin li a::before {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  transition: all 0.3s;
}
.nav-admin li a span {
  font-size: 1.2rem;
  width: 0;
  opacity: 0;
  visibility: hidden;
  margin-left: 0rem;
  transform: scale(0);
  transition: all 0.3s;
}
.nav-admin li a:hover,
.nav-admin .li-active {
  color: var(--color-active);
  background-color: var(--color-hover-link);
  transition: all 0.3s;
}
.nav-admin li a:hover::before,
.nav-admin .li-active::before {
  background-color: var(--color-white);
  transition: all 0.3s;
}

@media (max-width: 624px) {
  .nav-admin {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s;
    z-index: 99;
  }
}
#burger__toggle {
  position: absolute;
  top: 1.8rem;
  left: 21.5rem;
  opacity: 0;
}

.burger__btn {
  position: absolute;
  top: 1.8rem;
  left: 21.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.2rem;
  height: 1.2rem;
  cursor: pointer;
}
.burger__btn span {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 0.2rem;
  background-color: var(--color-secondery);
  transition-duration: 0.3s;
}
.burger__btn span::before, .burger__btn span::after {
  position: absolute;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: var(--color-secondery);
  transition-duration: 0.3s;
}
.burger__btn span::before {
  content: "";
  top: 4px;
}
.burger__btn span::after {
  content: "";
  top: 8px;
}

#burger__toggle:checked ~ .burger__btn > span {
  transform: rotate(-45deg);
  transform-origin: 90% 90%;
}

#burger__toggle:checked ~ .burger__btn > span::before {
  top: 0;
  opacity: 0;
  transform: rotate(0);
}

#burger__toggle:checked ~ .burger__btn > span::after {
  top: 0;
  transform: rotate(90deg);
}

#burger__toggle:checked ~ .nav-active {
  position: relative;
  visibility: visible;
  opacity: 1;
  min-width: 20rem;
  transition: all 0.3s;
}
#burger__toggle:checked ~ .nav-active ul li a span {
  width: auto;
  opacity: 1;
  visibility: visible;
  margin-left: 1rem;
  transform: scale(1);
  transition: all 0.3s;
}

@media (max-width: 624px) {
  #burger__toggle {
    left: 12.5rem;
  }

  .burger__btn {
    left: 12.5rem;
  }

  #burger__toggle:checked ~ .nav-active {
    position: absolute;
    visibility: visible;
    opacity: 1;
    width: 100%;
    z-index: 1;
    transition: all 0.3s;
  }
  #burger__toggle:checked ~ .nav-active ul li a span {
    width: auto;
    opacity: 1;
    visibility: visible;
    margin-left: 1rem;
    transform: scale(1);
    transition: all 0.3s;
  }
}
.btn-select {
  width: 6px;
  height: 6px;
  border-right: 2px solid;
  border-bottom: 2px solid;
  border-color: var(--color-secondery);
  z-index: 1;
  transform: rotate(45deg);
}

.btn-logout {
  display: flex;
  width: 100%;
  align-items: center;
  flex-wrap: nowrap;
  padding: 1em 1.5em;
  margin: 0;
  background-color: var(--color-bg-main);
  border-top: 1px solid var(--color-border);
  transition: all 0.3s;
}
.btn-logout:hover {
  background-color: var(--color-border);
  transition: all 0.3s;
}
.btn-logout span {
  display: inline-block;
  color: var(--color-message);
  margin-right: 0.5em;
}

.btn-question {
  display: flex;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  color: var(--color-light);
  margin-right: 1rem;
  background-color: var(--color-bg-nav);
  border-radius: 50%;
}
.btn-question span {
  transform: translate(0, 0.2rem);
}

.btn-close {
  stroke: var(--color-white);
  transition: 0.3s ease;
  margin-left: auto;
}
.btn-close:hover {
  stroke: var(--color-shudow);
  transform: scale(1.2);
  transition: 0.3s ease;
}

.btn-chat {
  position: fixed;
  right: 10px;
  bottom: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  height: 5rem;
  background-color: var(--color-light);
  box-shadow: 0px 0px 8px 2px var(--color-shudow);
  border-radius: 50%;
}

.btn-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

@media (max-width: 530px) {
  .btn-question {
    display: none;
  }
}