.chat {
  position: fixed;
  right: 20px;
  bottom: 50px;

  opacity: 0;
  visibility: hidden;
  transform: scaleX(0);
  transform-origin: right center;
  transition: .3s ease;

  z-index: 10;
}



.chat__container {
  display: flex;
  flex-direction: column;

  width: 375px;
  height: 550px;

  background-color: var(--color-bg-main);
  box-shadow: 0px 0px 8px 2px var(--color-shudow);
  border-radius: 10px;
}

.chat__header {
  display: flex;
  align-items: center;
  padding: 10px 20px;

  color: var(--color-white);
  background-color: var(--color-active);
  border-radius: 10px 10px 0 0;
}

.admin-icon {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 70px;
  height: 70px;
  margin-right: 15px;

  border-radius: 50%;
  background-color: var(--color-bg-icon);
}

._icon-peaple {
  font-size: 3.5rem;
}

.admin-info {
  margin-right: 1rem;
}

.admin-name {
  font-size: 2.2rem;
  line-height: 2.6rem;
  margin-bottom: .5rem;
}

.admin-access {
  font-size: 1.2rem;
  letter-spacing: 1.2px;
}



.chat__body {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-y: auto;
  padding: 0 .5rem;
  margin: 1rem;

  border-bottom: 1px solid var(--color-border);
}

.chat__title {

  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.1rem;

  text-align: center;
  margin-bottom: 1.5em;
}

.message__manager,
.message__user {
  font-size: 1.4rem;
  margin-bottom: 1.5rem;
}

.message__info {
  font-size: 1.2rem;
  color: var(--color-secondery);
  margin-bottom: 1rem;
}

.message__manager-text {
  position: relative;
  display: block;
  width: 90%;
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: var(--color-bg-chat-manadger);

  border: 1px solid var(--color-border);
  border-radius: 5px;

  &::before,
  &::after {
    position: absolute;
    top: -8px;
    left: 8px;

    display: block;
    content: "";

    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 8px 8px 8px;
    border-color: transparent transparent var(--color-border) transparent;

  }


  &::after {
    position: absolute;
    top: -6px;
    left: 8px;

    border-color: transparent transparent var(--color-bg-chat-manadger) transparent;
  }
}

.message__user {
  text-align: right;
}

.message__user-text {
  position: relative;
  display: block;
  width: 90%;
  text-align: left;

  padding: 1rem;
  margin-bottom: 1rem;
  background-color: var(--color-bg-chat-user);

  border: 1px solid var(--color-border);
  border-radius: 5px;

  &::before,
  &::after {
    position: absolute;
    top: -8px;
    right: 8px;

    display: block;
    content: "";

    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 8px 8px 8px;
    border-color: transparent transparent var(--color-border) transparent;

  }


  &::after {
    position: absolute;
    top: -6px;
    right: 8px;

    border-color: transparent transparent var(--color-bg-chat-user) transparent;
  }
}



.chat__footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: .5rem 1rem 1rem;

  height: 10rem;
}

.chat__input {
  display: flex;
  width: 100%;
  height: 100%;
  resize: none;
  padding: 1rem;

  background-color: var(--color-bg-chat-user);

  border: 1px solid var(--color-border);
  border-radius: 5px;


  &::placeholder {
    font-size: 1.6rem;
    color: var(--color-shudow);
  }

  &:focus-visible,
  &:hover {
    outline: 0;
    background-color: var(--color-light);
  }
}

.chat-btns {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;

  svg {
    fill: var(--color-border);
    transition: .3s ease;

    &:hover {
      fill: var(--color-active);
      transition: .3s ease;
    }
  }

}

.chat__send {
  margin-bottom: 1rem;
}


.chat-active {
  opacity: 1;
  visibility: visible;
  transform: scaleX(1);
  transform-origin: right center;
  transition: .3s ease;
}

@media (max-width: 413px) {
  .chat {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .chat__container {
    width: 100%;
    height: 100%;
  }

}

@media (max-width: 385px) {
  .chat {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .chat__container {
    width: 100%;
    height: 100%;
  }

  .admin-icon {
    width: 50px;
    height: 50px;
    margin-right: 15px;
  }

  .admin-name {
    font-size: 1.8rem;
    line-height: 2.2rem;
  }

}

@media (max-height: 600px) {
  .chat {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .chat__container {
    width: 100%;
    height: 100%;
  }

  .admin-icon {
    width: 50px;
    height: 50px;
    margin-right: 15px;
  }

  .admin-name {
    font-size: 1.8rem;
    line-height: 2.2rem;
  }

}
