#burger__toggle {
  position: absolute;
  top: 1.8rem;
  left: 21.5rem;
  opacity: 0;
}

.burger__btn {
  position: absolute;
  top: 1.8rem;
  left: 21.5rem;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 1.2rem;
  height: 1.2rem;
  cursor: pointer;

  span {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: .2rem;
    background-color: var(--color-secondery);
    transition-duration: .3s;

    &::before,
    &::after {
      position: absolute;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
      background-color: var(--color-secondery);
      transition-duration: .3s;
    }

    &::before {
      content: '';
      top: 4px;
    }

    &::after {
      content: '';
      top: 8px;
    }
  }
}

#burger__toggle:checked~.burger__btn>span {
  transform: rotate(-45deg);
  transform-origin: 90% 90%;
}

#burger__toggle:checked~.burger__btn>span::before {
  top: 0;
  opacity: 0;
  transform: rotate(0);

}

#burger__toggle:checked~.burger__btn>span::after {
  top: 0;
  transform: rotate(90deg);

}



#burger__toggle:checked~.nav-active {
  position: relative;
  visibility: visible;
  opacity: 1;
  min-width: 20rem;
  transition: all .3s;

  ul {
    li {
      a {
        span {
          width: auto;
          opacity: 1;
          visibility: visible;
          margin-left: 1rem;
          transform: scale(1);
          transition: all .3s;
        }
      }
    }
  }
}







@media (max-width: 624px) {

  #burger__toggle {
    left: 12.5rem;
  }

  .burger__btn {

    left: 12.5rem;
  }

  #burger__toggle:checked~.nav-active {
    position: absolute;

    visibility: visible;
    opacity: 1;
    width: 100%;
    z-index: 1;
    transition: all .3s;

    ul {
      li {
        a {
          span {
            width: auto;
            opacity: 1;
            visibility: visible;
            margin-left: 1rem;
            transform: scale(1);
            transition: all .3s;
          }
        }
      }
    }
  }


}
