.btn-select {

  width: 6px;
  height: 6px;
  border-right: 2px solid;
  border-bottom: 2px solid;
  border-color: var(--color-secondery);

  z-index: 1;

  transform: rotate(45deg);

}

.btn-logout {
  display: flex;
  width: 100%;
  align-items: center;
  flex-wrap: nowrap;
  padding: 1em 1.5em;
  margin: 0;
  background-color: var(--color-bg-main);
  border-top: 1px solid var(--color-border);
  transition: all .3s;

  &:hover {
    background-color: var(--color-border);
    transition: all .3s;
  }

  span {
    display: inline-block;
    color: var(--color-message);
    margin-right: .5em;
  }
}

.btn-question {
  display: flex;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  color: var(--color-light);

  margin-right: 1rem;
  background-color: var(--color-bg-nav);
  border-radius: 50%;

  span {
    transform: translate(0, .2rem);
  }
}

.btn-close {
  stroke: var(--color-white);
  transition: .3s ease;
  margin-left: auto;

  &:hover {
    stroke: var(--color-shudow);
    transform: scale(1.2);
    transition: .3s ease;
  }
}

.btn-chat {
  position: fixed;
  right: 10px;
  bottom: 50px;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 5rem;
  height: 5rem;
  background-color: var(--color-light);
  box-shadow: 0px 0px 8px 2px var(--color-shudow);
  border-radius: 50%;
}

.btn-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}


@media (max-width: 530px) {
  .btn-question {
    display: none;
  }
}
