.main {
  display: flex;


  .container {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    // overflow-y: auto;
    // overflow-x: hidden;

    // scrollbar-width: 0;

    // &::-webkit-scrollbar {
    //   width: 0;
    // }

    padding: 2rem 3rem;
  }

  .meassage {
    display: flex;

    min-height: 30rem;
    width: 100%;

    font-size: 1.8rem;
    padding: 1rem;
    margin-bottom: 3rem;
    background-color: var(--color-light);
    border: 1px solid var(--color-border);
  }
}

.tabs {
  margin-bottom: 2rem;

  input {
    display: none;

    &:checked+label {
      color: var(--color-black);
      background-color: var(--color-light);
    }
  }

  label {
    display: inline-block;
    padding: 1rem 2rem;
    margin-right: .2rem;

    color: var(--color-secondery);
    background-color: var(--color-border);

    border-top: 1px solid;
    border-left: 1px solid;
    border-right: 1px solid;
    border-color: var(--color-border);

    cursor: pointer;
  }


  .tabs__body {
    // position: relative;
  }

  .tabs__block {
    display: none;
    width: 100%;
    min-height: 20rem;

    font-size: 1.8rem;
    line-height: 2rem;
    padding: 2rem;
    background-color: var(--color-light);

    border-left: 1px solid;
    border-right: 1px solid;
    border-bottom: 1px solid;
    border-color: var(--color-border);

  }

  input[id="tab-1"]:checked~.tabs__body .tab-1,
  input[id="tab-2"]:checked~.tabs__body .tab-2,
  input[id="tab-3"]:checked~.tabs__body .tab-3 {
    display: block;
  }

}



@media (max-width:624px) {
  .main {
    .container {
      padding: 2rem;
    }
  }
}
