.nav-admin {
  min-width: auto;

  font-size: 2.4rem;
  padding-top: .8rem;
  margin-top: -.2rem;


  background-color: var(--color-bg-nav);
  transition: all .3s;



  ul {
    width: 100%;
    transition: all .3s;
  }


  li {
    width: 100%;
    margin-bottom: .2em;
    background-color: var(--color-bg-link);

    &:last-child {
      margin-bottom: 5em;
    }
  }

  li a {
    display: flex;
    align-items: center;
    padding: .5rem;
    padding-left: .8rem;
    color: var(--color-white);

    &::before {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 3.5rem;
      height: 3.5rem;
      border-radius: 50%;
      transition: all .3s;
    }

    span {
      font-size: 1.2rem;

      width: 0;
      opacity: 0;
      visibility: hidden;
      margin-left: 0rem;
      transform: scale(0);

      transition: all .3s;
    }
  }

  li a:hover,
  .li-active {
    color: var(--color-active);
    background-color: var(--color-hover-link);
    transition: all .3s;

    &::before {
      background-color: var(--color-white);
      transition: all .3s;
    }
  }

}



@media (max-width: 624px) {
  .nav-admin {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    transition: all .3s;
    z-index: 99;
  }
}
